<template>
    <div class="movieDetail">
        <div class="header-top">
            <van-nav-bar
                title="电影详情"
                left-text="返回"    
                left-arrow
                @click-left="back"
            />
        </div>

        <div class="movie-content-box">
 
            <div class="ba-img">
                <img :src="small" alt="">
            </div>
            <div class="content clearfix">
                <div class="img fl">
                    <img class="auto-img" :src="small" alt="">
                </div>
                <div class="title-box fl">
                    <div class="title movie-name">{{movieDetailData.title}}</div>
                    <div class="title number">{{average}}</div>
                    <div class="title movie-type">{{movieDetailData.genresInfo}}</div>
                    <div class="title movie-time">{{movieTime}}</div>
                    <div class="title time">上映时间:{{movieDetailData.pubdate}}</div>
                </div>
                <div class="bottom fl">
                    <div class="like fl">想看</div>
                    <div class="score fl">评分</div>
                </div>
            </div>

            <div class="introduce">{{movieDetailData.summary}}</div>

            <div class="performer-content-box">
                <div class="title">相关演员</div>
                <div class="performer-content" >
                    <div class="performer-box clearfix" :style="{width: length * 100 - 9 + 'px'}">
                        <div class="performer fl" v-for="(item,index) in movieDetailData.casts" :key="index">
                            <div class="img">
                                <img class="auto-img" :src="item.avatars.small" alt="">
                            </div>
                            <div class="name">{{item.name}}</div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="stills-content-box">
                <div class="title">电影剧照</div>
                <div class="stills-content" >
                    <div class="stills-box clearfix" :style="{width: length2 * 100 - 9 + 'px'}">
                        <div class="stills fl" v-for="(item,index) in movieDetailData.photos" :key="index">
                            <div class="img">
                                <img class="auto-img" :src="item.image" alt="">
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="comment-box">
                <div class="title">精选评论</div>
                <div class="comment-content " v-for="(item,index) in movieDetailData.popular_comments" :key="index">
                    <div class="user-img-box clearfix">
                        <div class="user-img fl">
                            <img class="auto-img" :src="item.author.avatar" alt="">
                        </div>
                        <div class="name fl">{{item.author.name}}</div>
                    </div>
                    <div class="comment-content-box">
                        <div class="comment-content-text">{{item.content}}</div>
                        <div class="time">{{item.created_at}}</div>
                    </div>

                </div>
            </div>

        </div>
        <van-button class="btn" type="danger" block @click="buyMovie({name:'buyMovie',params:{id:id}})">立即购票</van-button>
    </div>
</template>

<script>
    export default {
        name:'movieDetail',

        data(){
            return{ 
                id:'',
                movieDetailData:[],
                small:'',
                average:'',
                movieTime:'',
                length:'',
                length2:''
            }
        },

        created(){

            //截取路由参数
            let id = this.$route.params.id;
            this.id = id
            // console.log('id ==>',id);

            this.axios({
                method: 'GET',
                url: 'https://douban.uieee.com/v2/movie/subject/' + id
            }).then(result => {
                // console.log('result ==>',result)
                //将电影类型合并
                result.data.genresInfo = result.data.genres.join(' / ');

                this.movieDetailData = result.data
                // this.movieDetailData = this.movieDetailData.concat()
                // console.log('this.movieDetailData ==>',this.movieDetailData)
                this.small = this.movieDetailData.images.small
                this.average = this.movieDetailData.rating.average
                this.movieTime = this.movieDetailData.durations[0]
                this.length = this.movieDetailData.casts.length
                this.length2 = this.movieDetailData.photos.length
            }).catch(err => {
                // console.log('出错了')
            })
        },

        methods:{
            back(){
                this.$router.go(-1)
            },

            buyMovie(o){
                this.$router.push(o)
            }
        }
        
    }
</script>

<style lang="less" scoped>
.movieDetail{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(to bottom, #FED73E, #FFBE46);

    .movie-content-box{
        position: absolute;
        top: 46px;
        right: 0;
        left: 0;
        bottom: 44px;
        background-color: #f5f5f5;
        overflow-y: auto;

        .ba-img{
            height: 200px;
            width:100%;
            overflow: hidden;
            z-index:-1;
            opacity: 0.1;

            >img{
                width: 100%;
                display: block;
            }
        }

        .content{
            // position: relative;
            height: 180px;
            // width:100%;
            background-color:#ddd;
            margin-top: -200px;
            z-index: 5;
            padding: 10px;

            .img{
                width: 90px;
                height: 140px;
            }

            .title-box{
                margin-left: 10px;
                width:calc(~"100% - 100px");
                height:130px;
            }

            .title{
                font-size: 12px;
                max-width: 170px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                margin-bottom: 7px;
            }
            .movie-name{
                font-size: 18px;
                font-weight: bold;
                
            }
            .number{
                font-size: 16px;
                color: red;
                line-height: 16px;;
            }

            .bottom{
                width: 100%;
                height: 30px;
                // background-color: red;
                margin-top: 10px;
            }
            .like{
                width: calc(~"(100% - 10px) / 2");
                height: 100%;
                background-color:#fff;
                z-index: 3;
                text-align: center;
                line-height: 30px;
                margin-right: 10px;
            }
            .score{
                width: calc(~"(100% - 10px) / 2");
                height: 100%;
                background-color:#fff;
                z-index: 3;
                text-align: center;
                line-height: 30px;
            }
            
        }

        .introduce{
            padding: 10px;
            background-color: #fff;
            text-indent:25px
        }

        .performer-content-box{
            padding: 10px;
            background-color: #fff;
            margin-top: 10px;
            // overflow: hidden;

            .title{
                font-size: 18px;
                font-weight: bold;
                height:40px;
                line-height: 40px;
            }
            .performer-content{
                overflow-x: auto;
                overflow-y: hidden; 
            }
            .performer-box{
                overflow-x: auto;
                // width: 500px;
                height: 150px;
            }

            .performer{
                margin-right: 10px;
            }
            .performer:last-child{
                margin-right: 0px;
            }

            .img{
                width: 90px;
                height: 130px;
            }

            .name{
                text-align: center;
                line-height: 30px;
            }


        }

        .stills-content-box{
            padding: 10px;
            background-color: #fff;
            margin-top: 10px;

            .title{
                font-size: 18px;
                font-weight: bold;
                height:40px;
                line-height: 40px;
            }
            .stills-content{
                overflow-x: auto;
                overflow-y: hidden; 
            }
            .stills-box{
                overflow-x: auto;
            }

            .stills{
                margin-right: 10px;
            }
            .stills:last-child{
                margin-right: 0px;
            }

            .img{
                width: 90px;
                height:90px;
            }
            .name{
                text-align: center;
                line-height: 30px;
            }


        }
    }

    .comment-box{
        background-color: #fff;
        margin-top: 10px;
        padding: 10px;

        .title{
            line-height: 30px;
            font-weight: bold;
        }

        .comment-content{
            margin-bottom: 10px;
        }
        .user-img-box{
            width: 100%;
            height: 30px;
        }
        .user-img{
            width: 30px;
            height: 30px;
            border-radius: 50%;
            background-color: red;
            >img{
                border-radius: 50%;
            }
        }
        .name{
            width:calc(~"100% - 40px");
            line-height: 30px;
            margin-left: 10px;
        }
        .comment-content-box{
            width:calc(~"100% - 40px");
            margin-left: 40px;
            border-bottom: 1px solid #BFBFBF;
            bottom: 5px;
        }

        .time{
            color: #808080;
            line-height: 50px;
        }
    }

    .btn{
        position: fixed;
        bottom: 0;
    }
}

</style>